<template>
  <nav>
    <img class="nav-icon" src="@/assets/square.png" @click="navMenu()"/>
    <ul class="nav-menu">
      <li class="nav-link"><a href="#AboutMe">Обо мне</a></li>
      <li class="nav-link"><a href="#Skills">Навыки</a></li>
      <li class="nav-link"><a href="#Education">Образование</a></li>
      <li class="nav-link"><a href="#Portfolio">Портфолио</a></li>
      <li class="nav-link"><a href="#Contact">Контакты</a></li>
    </ul>
  </nav>
</template>

<script>
export default {
  name: "NavMenu",
  methods: {
    navMenu() {
      var x = document.querySelector(".nav-menu");
      if (x.classList.contains("responsive")) {
        x.classList.remove("responsive");
        x.classList.add("hide");
        setTimeout(this.disable, 1000, x)
      } else {
        x.classList.remove("hidden");
        x.classList.add("responsive");
      }
    },
    disable(x) {
      x.classList.remove("hide");
      x.classList.add("hidden");
    }
  },
};
</script>

<style scoped>

nav {
  height: 60px;
  z-index: 5;
  background-color: #1A1926;
}
.nav-menu {
  display: flex;
  list-style: none;
  justify-content: space-evenly;
  background-color: #1A1926;
}
li {
  padding: 16px;
  background-color: #1A1926;
}

.nav-icon {
  width: 28px;
  height: auto;
  display: none;
}

a {
    color: #E0E0E0;
    text-decoration: none;
  }

@media screen and (max-width: 840px) {
  .nav-menu {
    justify-content: space-between;
    margin: 40px 0 0 0;
    overflow: hidden;
    height: 0;
    flex-direction: column;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
  }

  .hidden {
    display: none;
  }

  .nav-icon {
    display: block;
    cursor: pointer;
    position: absolute;
    right: 0;
    top: 0;
    padding: 20px;
    z-index: 6;
  }

  nav {
    position: relative;
  }

  .responsive {
    display: flex;
    animation: drop 0.8s forwards;
  }

  @keyframes drop {
    from {height: 0;}
    to {height: 330px;}
  }

  .hide {
    animation: hide 0.8s forwards;
  }

  @keyframes hide {
    0% {height: 330px;
    display: flex;}
    90% {height: 0;
    display: flex;}
    100% {display: none;}
  }

  .nav-link {
    display: block;
    text-align: left;
    padding: 20px 0 20px 20px;
  }
}
</style>